import React, { useEffect, useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlay, faStop } from '@fortawesome/free-solid-svg-icons';
import { connect } from 'react-redux';

import { playMedia, stopMedia } from '../../redux/actions';
import styles from './RadioList.module.css';
import { getJson } from '../../services/httpService';
import { MEDIA_TYPE } from '../../constants';

function RadioList(props) {
    const [radios, setRadios] = useState([]);
    const playerState = props.playerState;
    const limit = props.limit || 20; // todo: page size is not supported at server side, load 20 then apply client side filter

    useEffect(() => {
        getJson('https://raw.githubusercontent.com/kodebot/api.vaanoli.kodebot.github.io/master/radio/tamil', false)
        .then(radios => {
           radios = radios.slice(0, limit);
            setRadios(radios);
        })
        .catch(error => console.error(error)); // todo: log??
    }, []);

    function togglePlay(radio) {
        if (playerState.isPlaying && playerState.mediaId === radio.id) {
            props.stopMedia();
        } else {
            props.playMedia({
                mediaType: MEDIA_TYPE.RADIO,
                mediaId: radio.id,
                src: radio.radioSources[0].url,
                imageUrl: radio.imageUrl,
                displayName: radio.name
            });
        }
    }

    function PlayStopButton({ radio }) {
        const isCurrentRadio = (playerState && playerState.mediaId) === radio.id;
        if (isCurrentRadio && playerState.isBuffering) {
            return (
                <div className={"spinner-border text-primary " + styles["player-button"]} role="status">
                    <span className="sr-only">Loading...</span>
                </div>
            );
        }

        if (isCurrentRadio && playerState.isPlaying) {
            return (
                <button className="btn btn-primary btn-lg btn-circle">
                    <FontAwesomeIcon icon={faStop} />
                </button>
            );
        }
        return (
            <button className="btn btn-primary btn-lg btn-circle">
                <FontAwesomeIcon icon={faPlay} />
            </button>
        );
    }

    return (
        <div className={styles["radio-list"]} >
            <ul>
                {radios.map(radio => (
                    <li key={radio.id} className={styles["radio-list-item"]} onClick={() => togglePlay(radio)}>
                        <div className={styles["radio-list-item-thumbnail"]}>
                            <img src={radio.imageUrl} alt=""></img>
                        </div>
                        <div className={styles["radio-list-item-content"]} >
                            <div className={styles["radio-list-item-content-details"]}>
                                <div className={styles["radio-list-item-content-details-title"]}>{radio.name}</div>
                                <div className={"badge badge-secondary " + styles["radio-list-item-content-details-badge"]}>{radio.country}</div>
                            </div>
                            <div className={styles["radio-list-item-buttons"]}>
                                <PlayStopButton radio={radio} />
                            </div>
                        </div>
                    </li>
                ))}
            </ul>
        </ div>
    )
}

const mapStateToProps = state => {
    const playerState = state.player;
    return { playerState };
}

export default connect(
    mapStateToProps,
    { playMedia, stopMedia }
)(RadioList);
