import React, { useState, useEffect, } from 'react';
import { withRouter, useLocation } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faChevronLeft, faChevronRight } from '@fortawesome/free-solid-svg-icons';
import EllipsisText from "react-ellipsis-text";

import { DateHumaniser } from '../common';

import styles from './NewsList.module.css';
import { getJson } from '../../services/httpService';

const PAGE_SIZE = 20; // page size is fixed at server side to 20

function useQuery() {
    return new URLSearchParams(useLocation().search);
}

function NewsList(props) {

    const query = useQuery();
    const currentPage = parseInt(query.get('page') || 1);
    const currentCategory = parseInt(query.get('category') || 1);

    const [data, setData] = useState([]);
    const [page, setPage] = useState(currentPage);
    const [categories, setCategories] = useState([]);
    const [categoryId, setCategoryId] = useState(currentCategory);

    const hideCategory = props.hideCategory || false;
    const limit = props.limit || 20; // todo: server side page size not supported yet

    useEffect(() => {
        getJson(`article/list?category=${categoryId}&page=${page}`)
            .then(data => {
                data = data || [];
                data = data.slice(0, limit);
                setData(data || []);
                window.scrollTo({ top: 0, left: 0, behavior: 'smooth' });
            })
            .catch(error => console.error(error)); // todo: log??
    }, [page, categoryId]);

    useEffect(() => {
        getJson('category/list')
            .then(categories => {
                setCategories(categories || []);
            })
            .catch(error => console.error(error)); // todo: log??
    }, []);

    function itemClicked(id) {
        props.history.push(`/article/?id=${id}`);
    }

    function next() {
        const nextPage = page + 1;
        props.history.push(`/news?category=${categoryId}&page=${nextPage}`)
        setPage(nextPage);
    }

    function previous() {
        const prevPage = page - 1;
        props.history.push(`/news?category=${categoryId}&page=${prevPage}`)
        setPage(prevPage);
    }

    function changeCategory(id) {
        if (categoryId !== id) {
            props.history.push(`/news?category=${id}&page=1`)
            setPage(1);
            setCategoryId(id);
        }
    }

    function PreviousButton() {
        if (page > 1) {
            return (
                <button type="button" className="btn btn-primary" onClick={previous} ><FontAwesomeIcon icon={faChevronLeft} /> &nbsp; Previous</button>
            );
        }

        return null;
    }

    function NextButton() {
        if (data.length === PAGE_SIZE) {
            return (
                <button type="button" className="btn btn-primary" onClick={next}>&nbsp;&nbsp;&nbsp;Next&nbsp;&nbsp;&nbsp;<FontAwesomeIcon icon={faChevronRight} /></button>
            );
        }

        return null;
    }

    function CategoryLink(props) {
        const currentCategory = parseInt(query.get('category') || 1);
        const style = props.category.ID === currentCategory ? 'nav-link active' : 'nav-link';
        return (
            <li className="nav-item">
                {
                    // eslint-disable-next-line jsx-a11y/anchor-is-valid
                    <a className={style} onClick={() => changeCategory(props.category.ID)}>{props.category.CategoryDisplayName}</a>
                }
            </li>
        );
    }

    return (
        <div className={styles["news-list-container"]}>

            {
                !hideCategory && (
                    <ul className="nav nav-pills">
                        {categories.map(category => (
                            <CategoryLink key={category.ID} category={category} />
                        ))}
                    </ul>)
            }

            <div className={styles["news-list"]}>
                <ul className="row">
                    {data.map(item => (
                        <li key={item.ID} className={styles["news-list-item"] + ' col-12 col-md-4'} onClick={() => itemClicked(item.ID)}>
                            <div className={styles["news-list-item-inner"] + ' row h-100 text-center'}>
                                {item.ThumbImageURL && (<div className="col-auto col-md-12">
                                    <img src={item.ThumbImageURL} alt=""></img>
                                </div>)}
                                <div className="col col-md-12">
                                    <div className={styles["news-list-item-title"]}>
                                        <EllipsisText text={item.Title} length={120} />
                                    </div>
                                </div>
                                <div className="w-100" />
                                <div className={styles["news-list-item-footer"] + ' row col align-self-end'}>
                                    <div className="col-auto text-left">
                                        <img alt="" src={'https://api.vaanoli.kodebot.com/news/images/feed-sources/' + item.Source + '.png'} />
                                    </div>
                                    <div className="col-auto pt-1 text-right align-self-center">
                                        <DateHumaniser value={item.PublishedAt} />
                                    </div>
                                </div>
                            </div>
                        </li>
                    ))}
                </ul>
                <div className={styles["news-list-pagination"]}>
                    <PreviousButton />
                    <NextButton />
                </div>
            </div>
        </div>
    );

}

export default withRouter(NewsList);