import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlay, faStop } from '@fortawesome/free-solid-svg-icons';
import ReactPlayer from 'react-player';
import { connect } from 'react-redux';

import { playMedia, stopMedia, startBufferMedia, endBufferMedia } from '../../redux/actions';
import styles from './Player.module.css';

function Player(props) {

    if (!props.playerState.mediaId) {
        return null; // no media selected so don't show player
    }

    function togglePlay() {
        if (props.playerState.isPlaying) {
            props.stopMedia();
        } else {
            props.playMedia({
                mediaType: props.playerState.mediaType,
                mediaId: props.playerState.mediaId,
                src: props.playerState.src,
                imageUrl: props.playerState.imageUrl,
                displayName: props.playerState.displayName
            });
        }
    }

    function onBuffering(isBuffering) {
        if (isBuffering) {
            props.startBufferMedia();
        } else {
            props.endBufferMedia();
        }
    }

    function PlayerControl() {
        if (props.playerState.isBuffering) {
            return (
                <div className={"spinner-border text-primary " + styles["player-button"]} role="status">
                    <span className="sr-only">Loading...</span>
                </div>
            );
        }

        return (
            <button className={"btn btn-primary btn-lg btn-circle " + styles["player-button"]} onClick={togglePlay}> <FontAwesomeIcon icon={props.playerState.isPlaying ? faStop : faPlay} /> </button>
        );
    }

    return (
        <div className={styles["player"]}>
            <div className={styles["player-image"]}>
                <img src={props.playerState.imageUrl} alt=""></img>
            </div>
            <div className={styles["player-name"]}>{props.playerState.displayName}</div>
            <PlayerControl />
            <ReactPlayer
                url={props.playerState.isPlaying ? props.playerState.src : 'none'} // HACK: to stop streaming when paused
                config={{
                    file: {
                        attributes: {
                            preload: 'none'
                        },
                        forceAudio: true
                    }
                }}
                style={{display: 'none'}}
                playing={props.playerState.isPlaying}
                onBuffer={() => onBuffering(true)}
                onBufferEnd={() => onBuffering(false)}
            />

        </div>
    );
}

const mapStateToProps = state => {
    const playerState = state.player;
    return { playerState };
}

export default connect(
    mapStateToProps,
    { playMedia, stopMedia, startBufferMedia, endBufferMedia }
)(Player);