import React from 'react';
import { withRouter, useLocation, Redirect } from 'react-router';

import { RadioList } from '../radio';
import { NewsList } from '../news';

import './Home.scss';


function useQuery() {
    return new URLSearchParams(useLocation().search);
}

function Home(props) {

    const query = useQuery();

    function viewAllRadiosHandler() {
        props.history.push('radios');
    }

    function viewMoreNewsHandler() {
        props.history.push('news');
    }


    // this is to handle page refresh
    const section = query.get('s');
    if (section) {
        query.delete('s');
        let remainingSearch = query.toString();
        remainingSearch = remainingSearch && `/?${remainingSearch}`;

        switch (section.toLowerCase()) {
            case 'article':
                return (<Redirect to={`/article${remainingSearch}`} />);
            case 'news':
                return (<Redirect to={`/news${remainingSearch}`} />);
            case 'radios':
                return (<Redirect to={`/radios${remainingSearch}`} />);
            default:
                return (<Redirect to='/' />);
        }
    }

    return (
        <div className="Home-body">
            <div className="Home-list">
                <h2 className="Home-header">Radios</h2>
                <RadioList limit={5} />
                <button type="button" onClick={viewAllRadiosHandler} className="btn btn-link"> VIEW ALL RADIOS</button>
            </div>
            <div className="Home-list">
                <h2 className="Home-header">Latest News</h2>
                <NewsList hideCategory={true} limit={12} />
                <button type="button" onClick={viewMoreNewsHandler} className="btn btn-link"> VIEW MORE NEWS</button>
            </div>
        </div>
    );
}

export default withRouter(Home);