import { PLAY_MEDIA, STOP_MEDIA, START_BUFFER_MEDIA, END_BUFFER_MEDIA } from "../actionTypes";

const initialState = {
    mediaType: null,
    mediaId: 0,
    src: null,
    imageUrl: null,
    displayName: null,
    isPlaying: false,
    isBuffering: false
};

export default function (state = initialState, action) {
    switch (action.type) {
        case PLAY_MEDIA: {
            return {
                ...state,
                mediaType: action.payload.mediaType,
                mediaId: action.payload.mediaId,
                src: action.payload.src,
                imageUrl: action.payload.imageUrl,
                displayName: action.payload.displayName,
                isPlaying: true,
            }
        }
        case STOP_MEDIA: {
            return {
                ...state,
                isPlaying: false,
            }
        }
        case START_BUFFER_MEDIA: {
            return {
                ...state,
                isBuffering: true,
            }
        }
        case END_BUFFER_MEDIA: {
            return {
                ...state,
                isBuffering: false,
            }
        }
        default: {
            return state;
        }
    }
}