import React from 'react';
import { NavLink } from 'react-router-dom';

import styles from './Navbar.module.css';

function Navbar() {
    return (
        <nav className="navbar navbar-expand-lg navbar-light bg-light">
            <a className="navbar-brand" href="/">
                <img src="/mainlogo.png" width="30" height="30" className="d-inline-block align-top" alt="" />
                <span className="pl-1">Tamiltent</span>
            </a>
            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarContent" aria-controls="navbarContent" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
            </button>

            <div className="collapse navbar-collapse" id="navbarContent">
                <ul className="navbar-nav mr-auto">
                    <li className="nav-item" data-toggle="collapse" data-target=".navbar-collapse.show">
                        <NavLink className="nav-link" activeClassName="active" exact to="/">Home</NavLink>
                    </li>
                    <li className="nav-item" data-toggle="collapse" data-target=".navbar-collapse.show">
                        <NavLink className="nav-link" activeClassName="active" exact to="/radios">Radios</NavLink>
                    </li>
                    <li className="nav-item" data-toggle="collapse" data-target=".navbar-collapse.show">
                        <NavLink className="nav-link" activeClassName="active" exact to="/news">News</NavLink>
                    </li>

                </ul>
                {/* <form className="form-inline my-2 my-lg-0">
                    <input className="form-control mr-sm-2" type="search" placeholder="Search" aria-label="Search" />
                    <button className="btn btn-outline-success my-2 my-sm-0" type="submit">Search</button>
                </form> */}
            </div>
        </nav>
    );
}

export default Navbar;