import React from 'react';
import { BrowserRouter, Route, Switch, useLocation } from 'react-router-dom';
import { Provider } from 'react-redux';

import './App.css';

import store from './redux/store';

import { Navbar } from "./components/navbar";
import { Home } from './components/home';
import { NewsList } from './components/news';
import { Article } from './components/article';
import { RadioList } from './components/radio';
import { Player } from './components/player';

function App() {

  return (
    <div className="App container-fluid p-0">
      <BrowserRouter>
        <Provider store={store}>
          <Navbar />
          <article className="App-body">
            <div className="App-body-content row no-gutters">
              <div className="d-none d-lg-block col-lg-2 App-box">
                {/* todo: ads and social media links  */}
              </div>
              <div className="col col-md-8 col-lg-6">
                <Switch>
                  <Route exact path="/article" render={(props) => (
                    <Article {...props}></Article>
                  )}>
                  </Route>
                  <Route exact path="/news">
                    <NewsList></NewsList>
                  </Route>
                  <Route exact path="/radios">
                    <div><RadioList></RadioList></div>
                  </Route>
                  <Route exact path="/">
                    <Home />
                  </Route>
                </Switch>
              </div>
              <div className="d-none d-md-block col-md-4 col-lg-4 App-box">
                {/* todo: ads and social media links  */}
              </div>
            </div>
            <Player />
          </article>
        </Provider>
      </BrowserRouter>
    </div>
  );
}

export default App;
