import { PLAY_MEDIA, STOP_MEDIA, START_BUFFER_MEDIA, END_BUFFER_MEDIA } from "./actionTypes";

export const playMedia = mediaInfo => ({
    type: PLAY_MEDIA,
    payload: {
        ...mediaInfo
    }
})

export const stopMedia = () => ({
    type: STOP_MEDIA,
    payload: {}
})

export const startBufferMedia = () => ({
    type: START_BUFFER_MEDIA,
    payload: {}
})

export const endBufferMedia = () => ({
    type: END_BUFFER_MEDIA,
    payload: {}
})