import React, { useState, useEffect } from 'react';
import { Redirect, useLocation } from 'react-router';
import { Helmet } from 'react-helmet';

import { DateHumaniser } from '../common';

import styles from './Article.module.css';
import { getJson } from '../../services/httpService';

function useQuery() {
    return new URLSearchParams(useLocation().search);
}

function Article(props) {

    const query = useQuery();
    const [article, setArticle] = useState({});
    const id = query.get('id');

    useEffect(() => {
        if (id) {
            getJson(`article/${id}`)
                .then(item => {
                    setArticle(item);
                    window.scrollTo({ top: 0, left: 0, behavior: 'smooth' });
                })
                .catch(error => console.error(error)); // todo: log error
        }
    }, []);

    if (!id) {
        return (<Redirect to='/news' />);
    }

    return (
        <div className={styles["article-content-container"]}>
            <Helmet>
                <link rel="canonical" href={article.SourceURL} />
            </Helmet>
            <h1 className={styles['article-header']}>{article.Title}</h1>
            <div className={styles['article-metadata'] + ' row'}>
                <div className="col-6 text-left">
                    <img alt="" src={'https://api.vaanoli.kodebot.com/news/images/feed-sources/' + article.Source + '.png'} />
                </div>
                <div className="col-6 text-right align-self-center">
                    <DateHumaniser value={article.PublishedAt} />
                </div>

            </div>
            <div className={styles["article-content"]} dangerouslySetInnerHTML={{ __html: article.Content }} />
            <a className="btn btn-primary my-2 my-sm-0" target="_blank" rel="noopener noreferrer" href={article.SourceURL} >Read original article</a>
        </div>
    );

}

export default Article;